* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Poppins", sans-serif;
}

section {
  height: 100vh;
  /* background:radial-gradient(white, darkcyan); */
  background-color: darkcyan;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}
.create-container {
  position: relative;

  padding: 50px;
  background-color: whitesmoke;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 5px rgb(0, 0, 0, 0.2);
  text-align: center;
}
.note {
  padding: 20px 0px;
}
.create-container h2 {
  color: #333;
}
.create-container input {
  border: 1px solid darkcyan;
  width: 100%;
  border-radius: 2px;
  outline: none;
  padding: 5px 5px;
  margin-bottom: 15px;
}

.create-container button {
  border: 1px solid darkcyan;
  border-radius: 2px;
  outline: none;
  background-color: darkcyan;
  padding: 5px 8px;
  color: white;
  margin-bottom: 25px;
}
.create-container button:hover {
  background-color: white;
  color: darkcyan;
}

.create-home {
  position: absolute;
  top: -50px;
  right: 20px;
  transform: scale(1.5);
}

.create-home-btn {
  color: white;
}
