body {
  /* max-width: 800px; */
  background-color: darkcyan;
  font-family: "Poppins", sans-serif;
}

section {
  /* height: 100vh; */
  position: relative;
  width: 100%;
}

/* messages  */
.chat-container {
  width: 50vw;
  height: 80vh;
  border: 2px solid #dedede;
  background-color: #f1f1f1;
  border-radius: 5px;
  /* padding: 0px 10px; */
  margin-bottom: 15px;
  overflow-y: scroll;
}

/* width */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}

.darker {
  border-color: #ccc;
  background-color: #ddd;
}

.sender {
  padding: 10px;
  text-align: right;
  /* background-color: lightgreen; */
}
.sender > p {
  color: white;
  background-color: rgb(12, 144, 184);
  display: inline-block;
  padding: 10px;
  margin: 5px;
  border-radius: 20px;
}
.receiver > p {
  color: white;
  background-color: rgb(10, 145, 77);
  display: inline-block;
  padding: 10px;
  margin: 5px;
  border-radius: 20px;
}

.receiver {
  padding: 10px;
  text-align: left;
}

.chat-profile-right {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 5px;
  align-items: center;
}
.chat-profile-left {
  display: flex;
  justify-content: flex-start;
  margin-bottom: 5px;
  align-items: center;
}

.chat-profile-userName {
  font-size: 15px;
  font-weight: 400;
}

.chat-profile-image-right {
  height: 40px;
  border-radius: 50%;
  margin-left: 10px;
}

.chat-profile-image-left {
  height: 40px;
  border-radius: 50%;
  margin-right: 10px;
}

.chat-user-entry {
  text-align: center;
  font-size: 0.5rem;
  margin-bottom: 10px;
  width: 100%;
}

.chat-user-entry > p {
  border: 1px solid #444;
  border-radius: 20px;
  background-color: whitesmoke;
  padding: 5px 10px;
  width: 50%;
  text-align: center;
  display: inline-block;
}

.chat-header {
  position: relative;
  padding: 5px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.chat-close {
  color: white;
  transform: scale(2);
  cursor: pointer;
}

.chat-close-link {
  font-size: 28 px;
  text-decoration: none;
}
.chat-title {
  font-weight: 400;
  color: white;
}

.chat-send > form {
  width: 100%;
  display: flex;
}
.chat-send form > input {
  border: 1px solid darkcyan;
  /* width: 90%; */
  height: 40px;
  border-radius: 2px;
  outline: none;
  flex: 1;
  padding: 5px 5px;
  margin-bottom: 15px;
}
.chat-send form > button {
  border: 1px solid darkcyan;
  border-radius: 2px;
  /* width: 10%; */
  height: 40px;
  outline: none;
  background-color: darkcyan;
  padding: 5px 8px;
  color: white;
  margin-bottom: 25px;
}

.chat-send form > button:hover {
  background-color: white;
  color: darkcyan;
}

@media only screen and (max-width: 600px) {
  .chat-container {
    width: 100vw;
  }
  .chat-send {
    padding: 0px 20px;
  }
}
@media only screen and (max-width: 900px) {
  .chat-container {
    width: 100vw;
  }
  .chat-send {
    padding: 0px 20px;
  }
}
@media only screen and (max-width: 1100px) {
  .chat-container {
    width: 100vw;
  }
  .chat-send {
    padding: 0px 20px;
  }
}
@media only screen and (max-width: 400px) {
  .chat-header {
    padding: 5px 10px;
  }
}
