.home-container {
  padding: 50px;
  /* width: 300px; */
  position: relative;
  background-color: whitesmoke;
  border-radius: 10px;
  box-shadow: 0px 0px 5px 5px rgb(0, 0, 0, 0.2);
  text-align: center;
}

section {
  height: 100vh;
  background-color: darkcyan;
  /* position: static; */
  display: flex;
  justify-content: center;
  align-items: center;
}

.home-title {
  text-align: center;
  top: -100px;
}

.home-title > h2 {
  font-weight: 400;
  font-size: 2rem;
  color: darkcyan;
  margin-bottom: 10px;
}

.home-container button {
  border: 1px solid darkcyan;
  border-radius: 2px;
  outline: none;
  background-color: darkcyan;
  padding: 5px 8px;
  color: white;
  margin: 15px;
}

.home-container button:nth-child(1) {
  /* margin-bottom: 25px; */
}
.home-container button:hover {
  background-color: white;
  color: darkcyan;
}

@media only screen and (max-width: 600px) {
  .home-container {
    width: 100vw;
  }
}
